import React from "react";
import { Controller, type Path, useFormContext } from "react-hook-form";
import type { GroupBase, OptionsOrGroups } from "react-select";
import FormField from "../../vendor/FormField.jsx";
import { Select as BaseSelect } from "../../vendor/Select/index.js";

export type SelectProps = {
  /**
   * If `true`, the component is disabled.
   *
   * @default false
   */
  disabled?: boolean | undefined;

  /**
   * An error message to display below the element when in an invalid state.
   */
  error?: { message?: string | undefined } | undefined;

  /**
   * Explanatory text to render below the element.
   */
  helpText?: React.ReactNode | undefined;

  /**
   * A user-friendly label displayed alongside the element.
   */
  label?: string | undefined;

  /**
   * A unique name identifying the element within a form.
   */
  name?: string | undefined;

  /**
   * <!-- TODO(ndhoule): Document me -->
   */
  options: { label: string; value: string }[] | undefined;

  /**
   * When `true`, the element will be invalid when touched and no selection has been made.
   *
   * @default false
   */
  required?: boolean | undefined;
};

/**
 * Select components give users the ability to choose one or more options from a list of choices.
 *
 * <!-- TODO(ndhoule): Add a screenshot here -->
 *
 * @param props
 * @see {@link Form}
 * @example
 * ```tsx
 * import { Select } from "@netlify/sdk/react/components";
 *
 * <Select
 *   label="Your Favorite Dog"
 *   name="favoriteDog"
 *   options={[
 *     { label: "Corgi", value: "corgi" },
 *     { label: "Golden Retriever", value: "golden-retriever" },
 *     { label: "Pomeranian", value: "pomeranian" },
 *   ]}
 * />
 * ```
 */
export const Select = <FieldValues extends Record<string, unknown>>({
  label,
  helpText,
  name,
  options,
  required,
  disabled,
  error,
  ...otherProps
}: {
  name: string;
  options: OptionsOrGroups<unknown, GroupBase<unknown>> | undefined;
  label?: string | undefined;
  helpText?: React.ReactNode;
  required?: boolean;
  disabled?: boolean;
  /**
   * An error message that overrides errors produced by built-in validation.
   */
  error?: { message: string } | undefined;
}) => {
  const { control } = useFormContext<FieldValues>();

  return (
    <Controller
      name={name as unknown as Path<FieldValues>}
      control={control}
      render={({ field, fieldState }) => {
        return (
          <FormField
            disabled={disabled}
            error={error?.message ?? fieldState.error?.message}
            helpText={helpText}
            label={label}
            control={
              <BaseSelect
                {...field}
                valueOnly
                required={required}
                options={options}
                {...otherProps}
              />
            }
          />
        );
      }}
    />
  );
};
